<template>
  <button type="button" class="btn" :class="`btn-${type}`" :data-dismiss="closeModal ? 'modal': ''">{{ title }}</button>
</template>

<script>
  export default {
    props: {
      type: {
        type: String,
        default: 'default'
      },
      title: {
        type: String,
        default: 'Button'
      },
      closeModal: {
        type: Boolean,
        default: false
      }
    }
  };
</script>



